import React from "react";
import "./Button.css";

const Button = props =>
  props.dummy ? (
    <div className="Button">{props.children}</div>
  ) : (
    <a
      className="Button"
      href={props.href}
      style={{
        display:
          props.href !== null && props.href && props.href.length > 1
            ? "inline-block"
            : "none"
      }}
    >
      {props.children}
    </a>
  );

export default Button;



