import React from "react";
import "./Card.css";

const Card = ({ children, title, clickable }) => (
  <div className={`Card ${clickable ? "Card-clickable" : ""}`}>
    {title && <div className="Card-title">{title}</div>}
    {children}
  </div>
);

export default Card;



