import React from "react";
import "./LabelValue.css";
const LabelValue = ({ label, value, medium }) => (
  <div className={["LabelValue", medium && "LabelValue-medium"].join(" ")}>
    <div className="LabelValue-label">{label}</div>
    <div className="LV-value">{value}</div>
  </div>
);

export default LabelValue;



