import React from "react";
import "./Preloader.css";
import Card from "./Card";

const Preloader = () =>
  <div className="Preloader">
    <FakeCard />
    <FakeCard />
    <FakeCard />
    <FakeCard />
  </div>;

const FakeCard = () =>
  <Card>
    <div className="Preloader-line-small" />
    <div className="Preloader-line" />
    <div className="Preloader-line-small" />
    <div className="Preloader-line-medium" />
    <div className="Preloader-line-small" />
    <div className="Preloader-line-medium" />
    <div className="Preloader-line-wide" />
  </Card>;

export default Preloader;



